<template>
  <div class="fauce-card">
    <div class="container-column">
      <div class="card">
        <form id="fauceApp" @submit="checkForm" novalidate="true">
          <div class="container">
            <img src="@/assets/img/logo-proximax-xpx-faucet.svg" alt="logo" style="width: 70%">
            <h2 class="text-color-title text-size-vm20 mt-2rem">
              <b>SIRIUS BC TESTNET</b>
            </h2>
            <p
              style="margin: 0;"
              class="text-color text-size-vm16"
            >Faucet Will only send if XPX is ledscthan 100</p>
          </div>
          <div class="input-class mt-2rem">
            <input
              maxlength="46"
              v-bind:class="[classValdiate]"
              style="width: 100%;"
              type="text"
              v-model="address"
              @input="checkForm($event)"
            >
          </div>
          <div class="div-alert-war">
            <div v-show="showValidate" :class="alertclass">
              <b
                class="text-color text-size-vm16-margin"
                style="margin-top: 0.51rem;"
              >{{msjValidate}}</b>
            </div>
          </div>

          <div class="button-class">
            <button :disabled="isDisabled" type="submit" class="btn" @click="sendFaucet()">SEND</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import Utils from "@/services/Utils.js";
export default {
  data() {
    return {
      alertclass: "",
      address: null,
      buttonValidate: true,
      classValdiate: "",
      showValidate: false,
      msjValidate: ""
    };
  },
  methods: {
    checkForm: function(e) {
      this.address = Utils.addressForm(this.address);
      if (Utils.isValid(this.address)) {
        this.showValidate = false;
        this.msjValidate = "";
        this.classValdiate = "success";
      } else {
        this.showValidate = true;
        this.msjValidate = "Address A has to be 40 characters long";
        this.isB = true;
        this.classValdiate = "error";
        this.alertclass = "alert-war";
      }
    },
    sendFaucet() {
      if (Utils.isValid(this.address)) {
        this.buttonValidate = false;
        this.$apiService
          .get(`faucet/GetXpx/${Utils.clean(this.address)}`)
          .then(response => {
            this.showValidate = false;
            this.msjValidate = "";
            this.classValdiate = "";
            this.buttonValidate = true;
            this.address = "";
            // this.$notify({
            //   title: "success!",
            //   group: "foo",
            //   text: `${response.data}`,
            //   type: "success"
            // });
            this.alertclass = "alert-success";
            this.showValidate = true;
            this.msjValidate = `${response.data}`;
            setTimeout(
              function() {
                this.showValidate = false;
              }.bind(this),
              15000
            );
          })
          .catch(error => {
            this.classValdiate = "";
            this.buttonValidate = true;
            this.address = "";
            let msj = null;
            msj = error.response.data.message
              ? error.response.data.message
              : error.response.data;
            this.alertclass = "alert-error";
            this.showValidate = true;
            this.msjValidate = `${msj}`;
            setTimeout(
              function() {
                this.showValidate = false;
              }.bind(this),
              10000
            );
            //VARC5G-OWFIWG-7JK7JV-Y7DXIS-TQYOID-75ON3G-O22H
            // console.log(`${msj}`)
            // this.$notify({
            //   group: "foo",
            //   title: "Error!",
            //   text: `${msj}`,
            //   type: "error"
            // });
          });
      }
    }
  },
  computed: {
    isDisabled: function() {
      return !this.buttonValidate;
    }
  }
};
</script>

